.case-card {
  position: relative;
  max-width: 268px;
  width: 100%;

  &__link-contain {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
  }

  &__wrapper {
    background: #FFFFFF;
    box-shadow: 0px 4px 30px rgba(80, 106, 153, 0.1);
    border-radius: 16px;
  }

  &__inner {
    padding: 16px 24px 24px;
  }

  &__img {
    line-height: 0;
    img {
      border-top-left-radius: 16px;
      border-top-right-radius: 16px;
      max-width: 100%;
      height: 156px;
      object-fit: cover;
      object-position: center;
    }
  }

  &__title {
    margin-top: 8px;
  }

  &__text {
    margin: 8px 0 42px;
  }
}
